$(document).ready(function() {

// services
$(".header-center__item").hover(onIn, onOut);
$(".header-center__item .header-center__item-show").click(function() {
    if ($(this).parents(".header-center__item").hasClass("header-center__item--active")) {
        $(this).parents(".header-center__item").removeClass("header-center__item--active")
    } else {
        $(this).parents(".header-center__item").addClass("header-center__item--active")
    }
})

// menu
 $('.header-center__burger').click(function () {
    $(this).toggleClass('header-center__burger--active')
    $('.header-center__nav').toggleClass('header-center__nav--active')
    $('body').toggleClass('fixed-body')
 })


})

// video
window.addEventListener('DOMContentLoaded', function () {

    var video = document.querySelector('.video');

    video.addEventListener('click', function () {
        if (video.classList.contains('video--active')) {
            return;
        }
        video.classList.add('video--active');
        var src = video.dataset.src;

        video.insertAdjacentHTML('afterbegin', '<iframe src="'+ src +'" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>')
    })

})

// services
function onIn() {
    if (window.innerWidth > 768) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-center__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 768) {
        $(this).removeClass("header-center__item--active")
    }
}